<template>
  <div id="app">
    <div class="header">
      <el-input
        placeholder="输入产品关键字"
        v-model="filterText">
      </el-input>
    </div>
    <div class="content">
      <div class="tree-wrap">
        <el-tree
          class="filter-tree"
          :data="treeData"
          default-expand-all
          :filter-node-method="filterNode"
          @node-click="handleNodeClick"
          ref="tree">
        </el-tree>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      filterText: '',
      treeData: [{
        id: 1,
        label: '一级 1',
        children: [{
          id: 4,
          label: '二级 1-1',
          children: [{
            id: 9,
            label: '电泳双轨自行输送小车作业安全操作规程.docx',
            url: 'http://www.xdocin.com/xdoc?_func=form&_key=oppax4hqf5gupmpayvf6ja7pcm&_xdoc=./%E7%94%B5%E6%B3%B3%E5%8F%8C%E8%BD%A8%E8%87%AA%E8%A1%8C%E8%BE%93%E9%80%81%E5%B0%8F%E8%BD%A6%E4%BD%9C%E4%B8%9A%E5%AE%89%E5%85%A8%E6%93%8D%E4%BD%9C%E8%A7%84%E7%A8%8B.docx'
          }, {
            id: 10,
            label: '三级 1-1-2'
          }]
        }]
      }, {
        id: 2,
        label: '一级 2',
        children: [{
          id: 5,
          label: '二级 2-1'
        }, {
          id: 6,
          label: '二级 2-2'
        }]
      }, {
        id: 3,
        label: '一级 3',
        children: [{
          id: 7,
          label: '二级 3-1'
        }, {
          id: 8,
          label: '二级 3-2'
        }]
      }]
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    handleNodeClick(data) {
      if(!data.children) {
        if(data.url) {
          window.open(data.url)
        }
      }
    }
  }
}
</script>

<style lang="less">
  html,body{
    width: 100%;
    height: 100%;
  }
  html,body,div,p,h3,ul,li{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  #app {
    height: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333;
    background: #f4f4f4;
    .header{
      height: 120px;
      padding: 30px 2%;
      background: #fff url(./assets/logo.png) 2% no-repeat;
      display: flex;
      justify-content: flex-end;
      .el-input{
        width: 60%;
        font-size: 18px;
        .el-input__inner{
          height: 100%;
        }
      }
    }
    .content{
      width: 100%;
      height: calc(100% - 120px);
      padding: 1%;
      .tree-wrap{
        width: 100%;
        height: 100%;
        background: #fff;
        border-radius: 8px;
        padding: 2%;
        .el-tree-node__label{
          font-size: 16px;
        }
        .el-tree-node__content{
          height: 30px;
        }
      }
    }
  }
</style>


